import React from "react";
import { NavLink, Outlet } from "react-router-dom";
import StickyBox from "react-sticky-box";
import categorySample from "../../assets/images/category-sample.jpg";
import "./Services.scss";

const Services = () => {   
    return (
        <div className="product-list-page">
          <div className="category-image-container">
            <img className="category-main-image" src={categorySample} alt="product-image"/>
          </div>
          <div className="container">    
            <div className="row my-5">
              <nav aria-label="breadcrumb" className="border">
                <ol className="breadcrumb">
                  <li className="breadcrumb-item"><a href="#">Home</a></li>
                  <li className="breadcrumb-item"><a href="#">Services</a></li>
                  <li className="breadcrumb-item active" aria-current="page">All</li>
                </ol>
              </nav>
            </div>      
            <div className="row product-container">
              <StickyBox offsetTop={20} offsetBottom={20} className="filter-sidebar border">                
                  <div className="product-filter-container">
                    <div className="title">Services</div>
                    <div className="filter-list">
                      <NavLink to="/services/work" className="filter-nav" end>Strategy work </NavLink>
                      <NavLink to="/services/work2" className="filter-nav" end>Identity design </NavLink>
                      <NavLink to="/services/work3" className="filter-nav" end>Creative Images</NavLink>
                      <NavLink to="/services/work4" className="filter-nav" end>flexible specialist </NavLink>
                      <NavLink to="/services/work5" className="filter-nav" end>Data Analysis</NavLink>
                      <NavLink to="/services/work6" className="filter-nav" end>Easy module </NavLink>
                    </div>
                  </div>
              </StickyBox>
              <div className="product-list border pb-3">
                <div className="row">
                  <div className="mobile-filter border-bottom">
                    <div className="total">300 Items</div>
                    <div className="mobile-filter-nav">
                      <span className="icon-set">
                        <i className="fa fa-filter"></i>
                      </span>
                    </div>
                  </div>
                </div>
                <Outlet/>
              </div>
            </div>
          </div>          
        </div>
    );
};

export default Services;
